import React from 'react';
import Accordion from 'react-bootstrap/Accordion';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import ButtonGroup from 'react-bootstrap/ButtonGroup';
import { Title, imgOption, optionGroup } from './QuickGuide';
import "./QuickGuide.css";

import puppy from "./img/QGdog/puppy.jpg";
import adult_dog from "./img/QGdog/adult_dog.jpg";
import special_dog from "./img/QGdog/special_dog.jpg";
import dog from "./img/QGdog/dog.png";

const age = [
    {id:'newborn', label:'', img:puppy}, 
    {id:'adult', label:'', img:adult_dog},
    {id:'special', label:'', img:special_dog}
];
const group0 = [
    {label:'', id:'01'}, 
    {label:'', id:'02'}, 
    {label:'', id:'03'}, 
    {label:'', id:'04'}, 
    {label:'', id:'05'}, 
];
const group1 = [
    {label:'', id:'11'}, 
    {label:'', id:'12'}, 
    {label:'', id:'13'}, 
    {label:'', id:'14'}, 
];
const group2 = [
    {label:'', id:'21'}, 
    {label:'/', id:'22'}, 
    {label:'', id:'23'}, 
    {label:'', id:'24'}, 
];
const group3 = [
    {label:'', id:'31'}, 
    {label:'', id:'32'}, 
    {label:'', id:'33'}, 
];
const group4 = [
    {label:'', id:'41'}, 
    {label:'()', id:'42'}, 
];
const healthyNewbornResult_dog = "!!";
const healthyAdultResult_dog = "";
const unwellResult_dog = "!!";
const unableToDetermine_dog = "!!";
const healthySpecialResult_dog = "Po!"

function QGDog(){
    return(
        <div>
            {Title(dog, "dogs", "")}
            {/* {getLocation} */}
            <Bird/>
        </div>
    );
}
export default QGDog;

class SelectAge extends React.Component {
    render() {
        if (this.props.activeSection === "age") {
            return (
            <>
            <div align='center'>
            <Form>
                <div className="add-more-padding">
                    {imgOption(age, this.props.onAgeSelect)}
                </div>
                <Button name="healthcheck" onClick={this.props.onNext}>Next</Button>
            </Form>
            </div>
            </>
        );
        } else {
            return null;
        }
    }
}

class HealthCheck extends React.Component {
    render() {
    if (this.props.activeSection === "healthcheck") { 
        return (
        <div align="center">
            <Form className="was-validated">
                <Accordion defaultActiveKey={['0', '1', '2', '3', '4']} alwaysOpen>
                    <div className='box'><Accordion.Item eventKey="0">
                        <Accordion.Header></Accordion.Header>
                        <Accordion.Body> 
                            {optionGroup("0", group0, this.props.onConditionSelect)}
                        </Accordion.Body></Accordion.Item></div>

                    <div className='box'><Accordion.Item eventKey="1">
                        <Accordion.Header></Accordion.Header>
                        <Accordion.Body> 
                            {optionGroup("1", group1, this.props.onConditionSelect)}
                        </Accordion.Body></Accordion.Item></div>

                    <div className='box'><Accordion.Item eventKey="2">
                        <Accordion.Header></Accordion.Header>
                        <Accordion.Body> 
                            {optionGroup("2", group2, this.props.onConditionSelect)}
                        </Accordion.Body></Accordion.Item></div>

                    <div className='box'><Accordion.Item eventKey="3">
                        <Accordion.Header></Accordion.Header>
                        <Accordion.Body> 
                            {optionGroup("3", group3, this.props.onConditionSelect)}
                        </Accordion.Body></Accordion.Item></div>

                    <div className='box'><Accordion.Item eventKey="4">
                        <Accordion.Header></Accordion.Header>
                        <Accordion.Body> 
                            {optionGroup("4", group4, this.props.onConditionSelect)}
                        </Accordion.Body></Accordion.Item></div>
                </Accordion> 
            </Form>
            <div className="submit-button">
                <ButtonGroup aria-label="Basic example">
                    <Button name="age" variant="secondary" size="lg" onClick={this.props.onBack}>back</Button> 
                    <Button type="submit" variant="primary" size="lg" onClick={this.props.onSubmit}></Button> 
                </ButtonGroup>
            </div> 
        </div>
        );
    } 
    else {
        return null;
    }
    }
}

class Result extends React.Component {   
    render() {
        const result = this.result;
        if (this.props.activeSection === "result") {
            return (
                <div align="center">
                <div className="result-container">
                    <h2></h2>
                    <p>{this.props.result}</p>
                    <p>:</p>
                        <p>1. !</p>
                        <p>2. </p>
                        <p>3. (..)</p>
                        <p>4. </p>
                        <p>5. </p>
                    <Button name="healthcheck" variant="secondary" size="lg" onClick={this.props.onBack}>back</Button> 
                </div>
                </div>
            );
        } else {
            return null;
        }
    }
}

